.switch {
  position: absolute;
  left: calc(50% - 700px);
  bottom: 30px;
  display: inline-block;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-size: 0;
  transition: opacity 0.25s;
}

.switch__input {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.switch__in {
  position: relative;
  display: inline-block;
  border: 8px solid #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  border-radius: 24px;
  transition: all 0.2s;
}

.switch__box {
  position: relative;
  display: block;
  width: 72px;
  height: 32px;
  box-shadow: inset 0 0 0 1px rgba(186, 195, 202, 0.4);
  background: #ffffff;
  border-radius: 16px;
  transition: all 0.2s;
}

.switch__box:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 4px;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #ff6e30;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  transition: all 0.2s;
}

.switch__icon {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  font-size: 0;
  transition: opacity 0.2s;
}

.switch__icon .icon {
  font-size: 24px;
  fill: #808191;
}

.switch__icon_light {
  right: 5px;
}

.switch__icon_dark {
  left: 4px;
  opacity: 0;
}

.switch__input:checked + .switch__in {
  border-color: rgba(0, 0, 0, 0.5);
}

.switch__input:checked + .switch__in .switch__box {
  box-shadow: inset 0 0 0 1px rgba(255, 255, 255, 0.1);
  background: rgba(0, 0, 0, 0.3);
}

.switch__input:checked + .switch__in .switch__box:before {
  width: 8px;
  height: 24px;
  -webkit-transform: translate(48px, -50%);
  transform: translate(48px, -50%);
  border-radius: 4px;
}

.switch__input:checked + .switch__in .switch__icon_light {
  opacity: 0;
}

.switch__input:checked + .switch__in .switch__icon_dark {
  opacity: 1;
}

